import React from "react";
import { lifecycle, compose } from "recompose";

import Layout from "components/layout";
import Header from "components/partner-landing-page/header";
import Hero from "components/careers/Hero";
import Description from "components/careers/Description";
import JobBoard from "components/careers/JobBoard";
import colors from "styles/colors";
import { useImageFormat } from "hooks";

const _Careers = ({ location }) => {
  const useWebP = useImageFormat();

  return (
    <Layout
      location={location}
      whiteContentBackground
      whiteGutterBackground
      title="Career Opportunities | Brex"
      description="Want to work at Brex? Explore all of our current job openings right here — from design to customer experience. Apply today & join our team!"
      hideDefaultHeader
    >
      <Header gutterBackground={colors.black} linkToHome />
      <Hero useWebP={useWebP} />
      <Description />
      <JobBoard />
    </Layout>
  );
};

const _lifecycle = {
  async componentWillMount() {
    // This script allows us to embed our Greenhouse job board in this page.
    // It renders wherever we add a div with an id of "grnhse_app" -- in this
    // case within the Description component.
    const greenhouseScript = document.createElement("script");
    greenhouseScript.src =
      "https://boards.greenhouse.io/embed/job_board/js?for=brex";

    greenhouseScript.onload = () => {
      // have to manually trigger this
      window.Grnhse.Iframe.autoLoad();
    };

    document.body.appendChild(greenhouseScript);
  },
};

export default compose(lifecycle(_lifecycle))(_Careers);
